var AbilityList=[{id:'A1',name:'出席',domain:'101',description:'工作出勤认真，除非有事情请假获准，不轻易缺席，有事不能工作时，会立即设法告知',assessMethod:'从日常生活中观察评量之',optionA:'具有下列行为之一者，属之： 1.经常未请假无故缺席。 2.经常请假（非必要的）。 3.在加班的非常情况下，缺席率增高。 4.需要一再地提醒才能正常出席',optionB:'凡合乎下列所有行为者，属之： 1.出勤认真，不轻易缺席。 2.有事会事先请假，获准后缺席。 3.即使临时有事，也会设法告知。',optionC:'凡通过b之标准外，并具下列行为之一者，属之： 1.经常保持全勤记录。 2.即使在加班的非常情况下，也不缺席。'},{id:'A2',name:'准时',domain:'101',description:'每天在规定的时间内准时上下班，在工作中的休息阶段（如：喝茶、如厕、午休）后，均能依规定准时返回工作岗位。',assessMethod:'从日常生活及工作中观察评量之',optionA:'具有下列行为之一者，属之： 1.经常型的迟到早退。 2.中途休息，容易延误时间回到工作场所。 3.工作中表现拖拖拉拉延误时效。 4.与人约定的时间，经常失约、误点（偶发不计） 5.需要一再的提醒，才能准时。',optionB:'凡合乎下列所有行为者，属之： 1.每天依规定的时间上下班，不迟到早退（偶发不计）。 2.工作中段的休息，能准时返回工作岗位。 3.不会有中途开溜的事。 4.与人约定的时间能准时赴约，极少失约误时。',optionC:'凡通过b之标准外，并具下列行为之一者，属之： 1.[限时性]的工作，如定时交货均能准时依约完成（超乎能力的工作不计）。 2.即使临时约定的时间（如临时加班）也能依时进行。 3.对于时间的安排特别用心（如提前准备、从容完成等）。。'},{id:'A3',name:'保持个人卫生的习惯',domain:'101',description:'外表整洁，望之无污秽感，如：头发、胡须、指甲及衣着之整饰，牙齿清洁无口臭，勤于沐浴，不生异味。',assessMethod:'从日常生活中观察评量之',optionA:'具有下列行为之一者，属之： 1.头发、胡须、指甲经常不修剪。 2.经常衣着不整洁，破绽不缝补。 3.身体生污秽后有异味、体臭。 4.给人一种外表污秽不洁的印象 ',optionB:'凡合乎下列所有行为者，属之： 1.头发、胡须、指甲勤于修剪。 2.衣着勤于换洗、修整、无不洁或破绽。 3.口腔干净、卫生、勤于刷洗，无食物残留，不生口臭。 4.身体勤于沐浴，不生异味。 5.予人外表整齐、清洁的印象。',optionC:'凡通过b之标准外，并具下列行为之一者，属之： 1.会自行适当的使用香水之类的物品化妆自己。 2.会自行适当食用饰物装扮自己（如发结、项链等）。'},{id:'A4',name:'无习惯性动作或语言',domain:'101',description:'没有若干不自觉的或难以控制的习惯性动作或言语（如：咬手指、口头禅、晃动身体、出怪声）',assessMethod:'从日常生活中观察评量之',optionA:'具有下列行为之一者，属之： 1.具有一种以上习惯性动作，其情节影响正常工作的进行及妨碍人际关系的正常发展。 2.具有三种以上轻微的习惯。 ',optionB:'凡合乎下列所有行为者，属之： 1.具有一种习惯动作，情节较轻微，不妨碍正常活动、人际关系的发展。 2.凡有两种习惯动作，但情况尚未构成人际关系及正常工作的进行。',optionC:'没有不自觉的或者难以控制的习惯动作或语言。如：咬手指头、口头禅、晃动身体、出怪声音等。'},{id:'A5',name:'礼貌',domain:'101',description:'在各种社交场所表现出如下行为：适当的歉意、问候、尊重他人、礼让他人、分享所有物、招待他人。',assessMethod:'从日常生活中观察评量之',optionA:'具有下列行为之一者，属之： 1.自己的所有物（如零食、用品。。。。）不借予他人使用，即使向其请求，仍然不肯借人。 2.即使在需要时也不会向他人致歉意。 3.在社交场所，给人一种粗鲁无礼的整体印象。 ',optionB:'凡合乎下列所有行为者，属之： 1.头发、胡须、指甲勤于修剪。 2.衣着勤于换洗、修整、无不洁或破绽。 3.口腔干净、卫生、勤于刷洗，无食物残留，不生口臭。 4.身体勤于沐浴，不生异味。 5.予人外表整齐、清洁的印象。',optionC:'凡合乎下列所有行为者，属之： 1.在社交场合的应对、举止合宜，表现突出，给人深刻印象。 2.在不同场合（如婚丧、喜庆等）能在衣着、行为或言语上表现出适当的行为。'},{id:'A6',name:'愉快',domain:'101',description:'保持愉快乐观的心性。若无严重刺激，则经常保持情绪稳定，不会大吼大叫或喜怒无常。',assessMethod:'从日常生活中观察评量之',optionA:'具有下列行为之一者，属之： 1.经常大吼大叫，随意动怒与人争辩。 2.稍遇挫折或刺激便会有消沉的行为表现。 3.经常予人一种情绪不稳定的印象。 ',optionB:'凡具有下列行为之一者，属之： 1.即使偶有不如意之事，仍能在最短的时间内恢复愉悦的心情。 2.能合理的控制脾气，以适当的方式表达内在的感受。 ',optionC:'凡通过b之标准外，并具下列行为之一者，属之： 1.经常予人一种乐观、愉快开朗的印象。 2.待人处事经常心存感激。'},{id:'A7',name:'诚实',domain:'101',description:'说实话，能坦诚错误，予他人信任感。',assessMethod:'从日常生活中观察评量之',optionA:'具有下列行为之一者，属之： 1.习惯性的说谎。 2.即使被人发觉说谎，提出纠正，仍然很难承担错误。 3.予人一种[不诚实]、[爱说谎]的印象。',optionB:'偶尔会有说谎的情行，但经纠正或笑以道理后，能坦承错误。',optionC:'凡合乎下列所有行为者，属之： 1.不管结果对自己有利或者有害均能坦承错误、不推诿。 2.予人一种[不说谎、很诚实]的印象。'},{id:'A8',name:'友善',domain:'101',description:'对工作伙伴表现出接纳的态度，没有排斥、挑剔、攻击、诋毁等拒绝的敌对态度',assessMethod:'对工作伙伴表现出接纳的态度，没有排斥、挑剔、攻击、诋毁等拒绝的敌对态度',optionA:'具有下列行为之一者，属之： 1.经常有喋喋不休、批评、诋毁、挑剔、攻击同伴的行为。 2.拒绝与同伴做善意的往来。 3.经常欺侮较弱小的同伴。 4.对新进的同伴不能表示接纳的态度甚或肆意排斥、敌对。',optionB:'凡合乎下列所有行为者，属之： 1.对熟悉的工作伙伴能接纳（不排斥）。 2.能适当的与工作伙伴沟通（以语言或动作）。 3.以适当的方式表达对同伴的善意（如微笑、协助、分享等）。',optionC:'凡通过b之标准外，并具下列行为之一者，属之： 1.经常予人一种乐观、愉快开朗的印象。 2.待人处事经常心存感激。'},{id:'A9',name:'动机',domain:'101',description:'能了解工作的意义与酬劳的关系，具有工作的医院，喜欢有事做，会主动追求工作机会，并努力保有此项工作。',assessMethod:'从日常生活中观察评量之',optionA:'具有下列行为之一者，属之： 1.缺乏工作意愿，终日赋闲在家，无所事事。 2.即使以物质性的诱因，也难诱发其工作意愿。 3.工作遇到挫折时，立即缺乏工作意愿。',optionB:'凡合乎下列所有行为者，属之： 1.工作的意愿高，喜欢有事做，不喜欢赋闲在家。 2.会担心失去工作，如被解雇等。 3.知道工作与酬劳的关系。',optionC:'凡通过b之标准外，并具下列行为之一者，属之： 1.会主动的追求工作机会如：找就业广告，请人介绍工作。 2.为了较长远的目标,能主动、持续地完成工作（长远目标：年终奖金、钱等）。'},{id:'A10',name:'努力',domain:'101',description:'工作时全力去做，表现出积极用心的态度，不会让人有[应付了事]、[得过且过]、[敷衍塞责]的感觉。',assessMethod:'从日常工作中观察评量之',optionA:'具有下列行为之一者，属之： 1.在一般工作场中表现消极态度。 2.需要一再督促才能继续工作。 3.予人[应付了事]、[得过且过]的印象。',optionB:'凡合乎下列所有行为者，属之： 1.在工作场中，表现出认真、用心、尽力去做的态度。 2.予人[积极用心]、[努力进取]的印象。',optionC:'凡通过b之标准外，并具下列行为之一者，属之： 1.即使是较困难的事（其能力较难胜任者）也表现出努力、认真的态度。 2.在非常的工作情境中（如加班赶工、高温、噪音）也能努力从事工作。'},{id:'B1',name:'工作姿势-站立',domain:'102',description:'在工作地点双脚保持直立不移动（不需双手的支持）。',assessMethod:'于平时工作中（或设计情境）观察评量之',optionA:'凡符合下列二者之一，属之： 1.虽有辅助器材，也无法持续站立着工作一小时以内。 2.仅能在一小时以内的时间中，持续站立着工作。',optionB:'能持续站立着工作一至二小时。',optionC:'能持续站立着工作三至四小时',optionD:'能持续站立着工作四小时以上。'},{id:'B2',name:'工作姿势-走动',domain:'102',description:'工作时间内以走动或辅助器材协助的方式持续地移动。',assessMethod:'设计工作情境观察评量之。',optionA:'凡符合下列二者之一，属之： 1.虽有辅助器材，也无法持续地来回走动一小时以内。 2.仅能在一小时以内的时间中，持续来回走动。',optionB:'能持续地来回走动一至二小时。',optionC:'能持续地来回走动三至四小时',optionD:'能持续地来回走动四小时以上。'},{id:'B3',name:'工作姿势-跑',domain:'102',description:'以快速的跑步方式持续地移动。',assessMethod:'于空旷的场地测试之。',optionA:'凡符合下列二者之一，属之： 1.虽有辅助器材，也无法连续跑完60公尺。 2.连续跑完60公尺所需要的时间超过15秒。',optionB:'能在15秒内连续跑完60公尺。',optionC:'能在13秒内连续跑完60公尺',optionD:'能在10秒内连续跑完60公尺。'},{id:'B4',name:'工作姿势-跨（跳）',domain:'102',description:'将身体以瞬发之力量带离地面之动作。',assessMethod:'设计不同的障碍物（如跳箱、积木、横杆等）观察评量之。',optionA:'凡符合下列二者之一，属之： 1.借着辅助器材，也无法跨（跳）过20公分高（远）的障碍物。 2.仅能跨（跳）过低于20公分高（远）的障碍物。',optionB:'能跨（跳）过20至30公分高（远）的障碍物。',optionC:'能跨（跳）过30至40公分高（远）的障碍物。',optionD:'能跨（跳）过40公分以上高（远）的障碍物。'},{id:'B5',name:'工作姿势-蹲',domain:'102',description:'曲膝并保持平衡的姿势持续一段时间。',assessMethod:'设计需要蹲着工作的情境观察评量之。',optionA:'凡符合下列二者之一，属之： 1.借着辅助器材，也无法持续蹲着工作达15分钟。 2.仅能在15分钟以内的时间中，持续蹲着工作。',optionB:'能持续蹲着工作达15分钟至半小时。',optionC:'能持续蹲着工作达半小时至一小时。',optionD:'能持续蹲着工作达一小时以上。'},{id:'B6',name:'工作姿势-跪',domain:'102',description:'膝盖着地以保持平衡的姿势。',assessMethod:'设计需要跪着工作的情境观察评量之。',optionA:'凡符合下列二者之一，属之： 1.虽有辅助器材，也无法维持跪姿工作达15分钟。 2.仅能在15分钟以内的时间中，持续跪着工作。',optionB:'能持续跪着工作达15分钟至半小时。',optionC:'能持续跪着工作达半小时至一小时。',optionD:'能持续跪着工作达一小时以上。'},{id:'B7',name:'工作姿势-坐',domain:'102',description:'以臀部着地，不依赖手部的协助而能持续保持平衡。',assessMethod:'设计需要坐着工作的情境观察评量之。',optionA:'凡符合下列二者之一，属之： 1.虽有辅助器材，也无法持续坐着工作达1小时。 2.仅能在15分钟以内的时间中，持续跪着工作。',optionB:'能持续坐着工作达一小时至二小时。',optionC:'能持续坐着工作达二小时至四小时。',optionD:'能持续坐着工作达四小时以上。'},{id:'B8',name:'工作姿势-爬',domain:'102',description:'帮助手掌和双膝在同一平面上交互移动，持续维持身体前进的姿势。',assessMethod:'设计需要爬着工作的情境观察评量之。',optionA:'凡符合下列二者之一，属之： 1.虽有辅助器材，也无法持续爬着工作。 2.仅能做出爬姿，但无法持续地以爬姿来进行工作。',optionB:'能持续地以爬姿进行5至15分钟的工作。',optionC:'能持续地以爬姿进行15分钟至半小时的工作。',optionD:'能持续地以爬姿进行半小时以上的工作'},{id:'B9',name:'工作姿势-躺',domain:'102',description:'以躯干卧于地板上的姿势，以便工作。',assessMethod:'设计需要躺着工作的情境观察评量之。',optionA:'凡符合下列二者之一，属之： 1.虽有辅助器材，也无法躺着工作。 2.虽能躺着，但无法持续躺着工作。',optionB:'能持续躺着工作半小时以内。',optionC:'能持续躺着工作半小时至一小时。',optionD:'能持续躺着工作一小时以上'},{id:'B10',name:'工作姿势-弯腰',domain:'102',description:'将腰部弯曲，保持工作时需要的姿势。',assessMethod:'设计需要弯腰的工作情境观察评量之。',optionA:'凡符合下列二者之一，属之： 1.虽有辅助器材，也无法弯腰工作（前弯或侧弯）。 2.需用辅助器材或手扶物才能做出前弯或侧弯的动作。',optionB:'凡符合下列二者之一，属之： 1.坐姿（椅子高度在60公分左右）时，能弯腰使手掌垂至膝盖以下，以便工作（取物等）。 2.站姿时，能弯腰使手掌垂至膝盖以下，以便工作。',optionC:'凡符合下列二者之一，属之： 1.坐姿（椅子高度在60公分左右）时，能弯腰使手掌垂至脚掌处，以便工作（取物等）。 2.站姿时，能弯腰使手掌垂至脚掌处，以便工作。'},{id:'C1',name:'家庭维持-烹饪的能力',domain:'103',description:'能依个人的喜好、营养及经济能力等因素，准备烹饪所需的食物。',assessMethod:'设计情境以评量之。',optionA:'需协助或指示，才能烹调自己的日常三餐。',optionB:'能顾及自己的喜好或营养或经济能力等条件之一，以烹调日常三餐。',optionC:'能兼顾自己的喜好、营养及经济能力等三个条件，以烹调日常三餐。'},{id:'C2',name:'家庭维持-家居的能力',domain:'103',description:'能以适当的方法与工具有效地维持家居环境与设备（厨房、客厅等清洗、打扫及家具的修护）。',assessMethod:'设计情境以评量之。',optionA:'需协助或指示才能完成维护家居环境的工作。',optionB:'能以扫帚、拖把、抹布、清洁剂等工具维护居家环境，并能维护清洁工具。',optionC:'能以吸尘器、消毒用品等工具维护居家环境，并能维护清洁工具。'},{id:'C3',name:'家庭维持-家居安全',domain:'103',description:'在家居生活中能正确、有效地维护个人与环境的安全（如电器、瓦斯、火烛、浴厕等）。',assessMethod:'从日常生活中或设计情境以观察评量之。',optionA:'需他人协助或指示能确保家居环境的安全。',optionB:'能按一般安全守则使用电器、瓦斯、火烛等，以防止意外发生。',optionC:'当意外灾害即将或已发生时，能使用有效的方法紧急处理以避免酿成更大灾害与损失，如：报警、灭火器的使用、开闭总开关、安全逃生等。'},{id:'C4',name:'金钱的管理-收入的计算',domain:'103',description:'能了解个人收入的计算方法（如：日薪、月薪、加班费、奖金等）并能做正确的核算。',assessMethod:'设计情境以评量之。',optionA:'需他人协助或指示始能完成个人所得的核算。',optionB:'能将个人的所得（如：日薪、月薪、加班费、奖金等）能做正确的核算',optionC:'能将个人营利的成本、开销及支出做出正确的计算，以核算个人所得（自己营业者）。'},{id:'C5',name:'金钱的管理-预算及支出',domain:'103',description:'能依个人收入状况做适当的支出预算，使收支平衡或有结余。',assessMethod:'设计情境以评量之。',optionA:'需他人协助或指示始能达成收支平衡。',optionB:'在固定收入状况下（如：日薪、月薪），能做适当的支出预算，使收支平衡或有结余。',optionC:'在较不稳定的收入状况下（如打零工、做小生意等）能做适当的支出预算，使收支平衡或有结余。'},{id:'C6',name:'金钱的管理-借贷',domain:'103',description:'需借贷时，能依个人的偿还能能力（如利息计算、诚信程度等相关因素），向他人或机关借贷。',assessMethod:'设计情境以评量之。',optionA:'需协助或指示始能与他人进行贷款者。',optionB:'需要借贷时，能依个人偿还能力及相关因素（如利息计算、诚信程度等）与亲友进行借贷，并能如约偿还。',optionC:'需要借贷时，能依个人偿还能力及相关因素（如利息计算、诚信程度等）向金融机构依法进行借贷，并能如约偿还。'},{id:'C7',name:'金钱的管理-储蓄',domain:'103',description:'能依个人经济条件，选择合适的储蓄方式（如银行、邮局存款、投保、活期存款、定期存款等）。并养成储蓄的习惯。',assessMethod:'从日常生活中观察评量之。',optionA:'需协助或指示始能完成储蓄的手续。',optionB:'需能依个人的经济条件，选择简易的储蓄方式（如简易邮政存金），并养成储蓄的习惯。',optionC:'能依个人的经济条件，并考量储蓄利息的计算及风险，选择最有利的储蓄方式，并养成储蓄的习惯。'},{id:'C8',name:'金钱的管理-纳税',domain:'103',description:'能依法律规定，办理应行缴交税款的手续',assessMethod:'设计情境以评量之。',optionA:'需他人协助或指示始能处理纳税问题者。',optionB:'能依规定完成简单税款的缴交手续，如拍照税、燃料税等。',optionC:'能依规定完成较复杂税款的填报手续，如个人综合所得税、公司营业税，并会使用节税方法。'},{id:'C9',name:'个人卫生与保健-涣洗与整饰',domain:'103',description:'能自行完成洗簌整饰，如洗脸、刷牙、梳整头发、配戴饰物等。',assessMethod:'从日常生活中观察评量之。',optionA:'需协助或提示始能完成洗簌整饰的动作。',optionB:'能自行完成洗簌整饰，如洗脸、刷牙、梳整头发等，保持干净的外表。',optionC:'能利用保养品、装饰品修饰自己的外表，让人产生良好的印象。'},{id:'C10',name:'个人卫生与保健-身心保健',domain:'103',description:'能以适当的可行的方式（如定时定量、运动、休息与睡眠、娱乐等）保持身心的健康。',assessMethod:'从日常生活中观察评量之。',optionA:'需协助或提示始能采取保健措施者。',optionB:'能使用适当的方式保持生理的健康，如饮食定时定量、运动、休息与睡眠。',optionC:'能以适当方式保持心理的健康如信仰、娱乐、交友等。'},{id:'C11',name:'个人卫生与保健-疾病医疗',domain:'103',description:'当生病时能自行就医，并依指示用药与保养以恢复健康。',assessMethod:'从日常生活中或设计情境以观察评量之。',optionA:'需帮助或提示始能前往医疗机构就医。',optionB:'当生病时能自行至一般社区诊所就医，并依指示用药与保健。',optionC:'当生病时能依病情需要至大医院求救，并依指示用药与保健。'},{id:'C12',name:'休闲生活-休闲资源的使用',domain:'103',description:'能适当的使用室内休闲资源（如电视机、收音机、扑克牌…)及户外休闲资源（如电影院、游泳池、百货公司）等设施。',assessMethod:'从日常生活中观察或访问与其接近的人以评量之。',optionA:'凡合乎下列行为之一者，属之：1.经口头提示或动作协助始能使用娱乐资源； 2.使用娱乐资源极有困难。',optionB:'能主动且独立地使用娱乐资源三种以上，如电视机、收音机、扑克牌、游泳池、百货公司等。',optionC:'能协助教导他人使用娱乐资源三种以上，如电视机、收音机、扑克牌、游泳池、百货公司等，并维护之。'},{id:'C13',name:'休闲生活-参与社区活动',domain:'103',description:'能够积极参与社区型或大团体的休闲活动（如：跳舞、球队等）。',assessMethod:'从日常生活中观察或访问与其接近的人以评量之。',optionA:'不主动参与社区活动或任何活动均无参加意愿。',optionB:'能主动参与一种以上的社区活动。',optionC:'能鼓励他人参与社区活动，如：跳舞、球队等，或协助社区活动的推行。'},{id:'C14',name:'休闲生活-活动安排的能力',domain:'103',description:'能依天气、地点、参与者年龄、节令、费用等因素来计划短时间或假日的活动。',assessMethod:'从日常生活中观察或访问与其接近的人以评量之。',optionA:'无法顾及天气、地点、参与者年龄、节令、费用等因素来计划活动。',optionB:'能依天气、地点、参与者年龄、节令、费用等因素协助主办者计划简易活动。',optionC:'能自行依天气、地点、参与者年龄、节令、费用等因素来计划活动。'},{id:'C15',name:'休闲生活-旅行能力',domain:'103',description:'能做好旅行前的准备（如：计划安排、食宿、联络、衣物、药品准备等），在旅行中并能注意个人安全、财务保管等问题',assessMethod:'从日常生活中观察或访问与其接近的人以评量之。',optionA:'需他人协助始能参与旅行。',optionB:'能跟随团体旅行，不造成他人的困扰，完成一趟安全愉快的旅行。',optionC:'能依个人旅行计划（如：膳食、行程、财务安全等）完成一趟安全愉快的旅行。'},{id:'C16',name:'购物与消费-购物场所',domain:'103',description:'能自行前往适当的商店购买所需的物品（如：到杂货店或超市买盐、酱油等）。',assessMethod:'从日常生活中观察或访问与其接近的人以评量之。',optionA:'需协助或指示始能前往商店购买所需物品。',optionB:'能自行前往社区内的小型商店购买日常所需的物品。',optionC:'能自行前往综合性的百货商店购买所需的物品。'},{id:'C17',name:'购物与消费-购物物品',domain:'103',description:'能根据购物要点（如新鲜卫生、价格合理、经济能力等）确定选购所需的物品。',assessMethod:'从日常生活中观察或访问与其接近的人以评量之。',optionA:'需他人协助或指示始能进行购买。',optionB:'能依个人需要自行选购物品。',optionC:'能依个人需要自行选购经济实惠、物美价廉的物品。'},{id:'C18',name:'购物与消费-购物手续',domain:'103',description:'能依照完整的购物手续（如列清单、准备钱、看标价、比价排队、付款等）购买到所需的物品。',assessMethod:'设计情境以评量之。',optionA:'需他人协助或指示始能进行购物手续。',optionB:'能依购物手续到社区购物中心购买少量物品。',optionC:'能依购物手续到超市、百货公司等大型购物中心购买多样物品。'},{id:'C19',name:'社交能力-家居礼仪',domain:'103',description:'在家居生活中，能自动表现出适当的礼节（如轻声耳语、衣着得体等）。',assessMethod:'从日常生活中观察或访问其接近的人以评量之。',optionA:'在家居生活中无法表现基本的生活礼节（如衣着得体等）。',optionB:'在家居生活中能表现基本的生活礼节（如衣得体等）。',optionC:'在家居生活中能表现出兄友弟恭、和睦相处等融洽的气氛。'},{id:'C20',name:'社交能力-睦邻',domain:'103',description:'和邻里亲朋往来时，能主动表现出友善的举止，如：应付得体、接受合宜、守望相助等。',assessMethod:'从日常生活中或访问与其接近的人以评量之。',optionA:'和邻里朋友往来时，不能主动表现守望相助、敦亲睦邻的举止。',optionB:'和邻里亲朋来往时，能主动表现守望相助、敦亲睦邻的举止。',optionC:'和邻里亲朋来往时，能主动表现出疾病相扶持的举止，并以宽谅的心情对待不友善的邻居。'},{id:'C21',name:'社交能力-社交礼节',domain:'103',description:'在社交场所中（如作客、待客、疾病慰问等）主动表现出适当的礼节（如拜访应事先联络、准时到达、探病时间不宜过久、声音不宜过大等）。',assessMethod:'从日常生活中或访问与其接近的人以评量之。',optionA:'在一般的社交场合中，不能主动表现出适当的礼仪（如打招呼、应对进退、疾病探视等）。',optionB:'在一般的社交场合中，能主动表现出适当的礼仪（如打招呼、应对进退、疾病探视等）。',optionC:'在特殊的社交场合中（如婚丧喜庆等）也能表现出得体的社交礼仪。'},{id:'B25',name:'感官知觉-大小辨别',domain:'102',description:'对物体或原型的大小（含长短粗细）的分辨能力。',assessMethod:'以实物观察评量之。',optionA:'对物体或原型的大小缺乏正确的分辨能力。',optionB:'能分辨大小差异较悬殊的物品。',optionC:'能分辨大小差异较细微的物品。'},{id:'B26',name:'感官知觉-色彩辨别',domain:'102',description:'对物体色彩的分辨力。',assessMethod:'准备不同色素及浓淡的物品观察评量之。',optionA:'对物体的色彩没有正确的分辨能力。',optionB:'能正确分辨不同色系的物体：如：红、蓝、黄、绿等。',optionC:'能正确分辨相同色系的物体，如：红、粉红、桃红等。'},{id:'B27',name:'感官知觉-空间知觉',domain:'102',description:'对三维空间的距离判断与空间关系的辨认。',assessMethod:'实测观察评量之。',optionA:'对三维空间的距离与空间关系缺乏正确的的辨认及判断能力。',optionB:'能分辨并指出在自己上下、左右、前后的位置。',optionC:'能正确地以上下、前后、左右、中间等空间关系的用语来描述一物品所在的位置。',optionD:'能依上下、前后、左右、中间等空间关系的要素，摆设出相关的物型。'},{id:'B28',name:'感官知觉-触觉',domain:'102',description:'以皮肤的感觉来分辨物体的大小、形状、资料及温度的不同。',assessMethod:'实测各种大小、形状、资料及温度差异性较大及较细微的物品。',optionA:'虽有触觉，但无法利用触觉来分辨物体的大小、形状及温度的大小。',optionB:'能以触觉来分辨在大小、形状、资料及温度上差异性较大的物品。',optionC:'能以触觉来分辨在大小、形状、资料及温度上差异性较细微的物品。'},{id:'B29',name:'感官知觉-听辨力',domain:'102',description:'辨别声音大小、高低、强弱的能力。',assessMethod:'设计情境观察评量之。',optionA:'虽有辅助性器材，对声音的大小、高低、强弱、也缺乏正确的分辨能力。',optionB:'能分辨差异性较大的声音，如：大小、高低、强弱。',optionC:'能分辨差异性较小的声音，如：大小、高低、强弱。'},{id:'B30',name:'感官知觉-听力',domain:'102',description:'察觉生声音，以耳接受声音讯息的能力。',assessMethod:'设计不同声音的情境观察评量之。',optionA:'仅能听见一公尺内的普通会话音。',optionB:'能听见一至三公尺内的普通会话音。',optionC:'能听见三至六公尺内的普通会话音。',optionD:'能听见六公尺以上的普通会话音。'},{id:'B31',name:'感官知觉-嗅味觉',domain:'102',description:'用舌头或鼻子分辨物体味道或气味（如：盐、糖、柠檬汁、香水等）的异同。',assessMethod:'利用数种不同味道的物品观察评量之。',optionA:'无法以舌头或鼻子正确地分辨物品的味道或气味。',optionB:'能分辨一般常见的物品的气味的异同，如：酸、甜、苦、辣、香、臭等。',optionC:'对于味道或气味差异轻微的物品，也能精确的分辨，如：各种不同厂牌的啤酒、香水等。'},{id:'B32',name:'协调能力-手眼协调',domain:'102',description:'视觉与手部（单手、双手）协调操作的能力，如：穿珠子、搬动物品等。',assessMethod:'设计情境观察评量之',optionA:'虽有辅助器材，也无法做手眼协调的操作。',optionB:'能做大动作的协调操作，如：以手搬动物品。',optionC:'能做小动作的协调动作，如：握、捻、穿珠子等。',optionD:'能使用工具做精细的手眼协调操作，如：精细的剪纸。'},{id:'B33',name:'协调能力-手脚协调',domain:'102',description:'手（单手、双手）与脚（单脚、双脚）同时协调运动的能力。',assessMethod:'设计情境观察评量之',optionA:'虽有辅助器材，也无法使手脚协调的操作。',optionB:'能做手脚同时协调的大动作，如：走路、爬行等。',optionC:'能做手脚同时协调的精细动作，如：装订、打起等。',optionD:'能配合使用工具做手脚同时协调操作的动作，如：手推车。'},{id:'B34',name:'协调能力-手眼脚协调',domain:'102',description:'视觉与手部（单手、双手）协调操作的能力，及脚部运动（单脚、双脚）同时配合的能力。',assessMethod:'设计情境观察评量之',optionA:'虽有辅助器材，也无法使用手脚眼协调的操作。',optionB:'能做手脚眼同时配合的大动作，如：攀爬鹰架等。',optionC:'能做手脚眼同时协调的精细动作，如：使用缝纫机等。',optionD:'能配合使用工具做手脚眼同时协调操作的动作，如：骑车、开车等。'},{id:'B35',name:'应付工作的足够体力',domain:'102',description:'完成工作所需要的足够生理耐力。',assessMethod:'设计情境观察评量之',optionA:'凡符合下列二者之一，属之： 1.没有具备应付轻工作的体力。 2.仅具备从事4个小时以内轻工作的体力（举重不超过5公斤）。',optionB:'具有从事至少四个小时以上轻工作的体力（举重不超过5公斤）。',optionC:'具有从事至少四个小时以上轻工作的体力（举重不超过10公斤）。',optionD:'具有从事至少四个小时以上轻工作的体力（举重不超过20公斤）。'},{id:'B36',name:'记住指示',domain:'102',description:'记忆他人口述的指示，并能正确复诵或完成。',assessMethod:'设计以口头指示的工作情境观察评量之。',optionA:'凡符合下列二者之一，属之： 1.不能复诵或做到他人口述的指示。 2.需他人提示才能正确复诵或做到单项口头指示。',optionB:'能正确复诵或做到单项口头指示。',optionC:'能正确做到复颂或做到多项口头指示。',optionD:'能将未组织的多项口头指示逐一完成。'},{id:'A27',name:'小组合作',domain:'101',description:'与伙伴合力完成工作，当伙伴要求配合时，能立即回应，合作过程有始有终。',assessMethod:'从日常生活或工作中观察评量之',optionA:'具有下列行为之一者，属之： 1.当伙伴或指导员要求其配合时，表现出消极或拒绝的态度。 2.独来独往，单独作业，不参与他人的工作或活动。 3.予人一种不合作的整体印象。 ',optionB:'凡合乎下列所有行为者，属之： 1.能与合作伙伴合力完成工作。 2.当伙伴要求配合时，能作立即的积极反应。 3.合作过程有始有终。',optionC:'凡通过b之标准外，并具下列行为之一者，属之： 1.能主动与他人寻求合作机会完成工作。 2.能引导他人合作参与，完成某项工作。 3.遇不良的合作伙伴时，能以宽谅心情继续采取合作的态度。 4.经常表现出助人的行为。 5.对新的工作伙伴也能表现出合作的态度。'},{id:'A28',name:'与上司合作',domain:'101',description:'对上司的要求（如工作安排、步骤指导、安全守则等）无论口头上或行动上均能乐意听从。',assessMethod:'从日常工作（或安排情境）观察评量之。',optionA:'具有下列行为之一者，属之： 1.对于上司的合理要求（如工作安排、步骤、安全规定等）经常表现出反对不服或口服心不服的态度。 2.对于新任的上司，常表现出批评、不合作的行为。 3.予人一种[不合作]、[不服从]的印象。 ',optionB:'凡合乎下列所有行为者，属之： 1.对于上司的合理要求（工作安排、步骤、安全规定等）均能遵守。 2.对上司提出的纠正乐于接受，并试图改正错误。 ',optionC:'凡通过b之标准外，并具下列行为之一者，属之： 1.对于频繁改变的规定也能遵守不渝，即使是不合理或对自己不利，也能尽量配合遵守。 2.对于新任上司能立即合作，接纳与服从。 '},{id:'A29',name:'工作品质',domain:'101',description:'工作中有[维持品质]的概念，在过程中能注意及遵守工作程序，随时检核工作成果，以确保工作品质，不会有[做了就算数]、[只求速度]、[不求品质]等情事',assessMethod:'从日常工作（或设计工作情境）观察评量之。',optionA:'具有下列行为之一者，属之： 1.随意变更、省略工作程序，明显地破坏工作品质。 2.工作成品约5%以上无法通过正常检验水平。 3.予人一种[草草了事]、[只求速度]、[不求品质]的印象。',optionB:'凡合乎下列所有行为者，属之： 1.工作中有随时检核产品品质的习惯。 2.对于不合理的产品会改善、调整使符合需求水准。 3.尽量遵守规定的作业程序，以确保品质，如作变更也不能影响品质。',optionC:'凡通过b之标准外，并具下列行为之一者，属之： 1.其所完成的产品品质在要求水准之上。 2.可负责品管的工作。 3.知道影响该产品品质的关键，做适当的改善。'},{id:'A30',name:'按时完成工作',domain:'101',description:'能准时完成具有时限性的工作，能努力维持工作进度，不随意拖延。',assessMethod:'从日常工作中观察评量之',optionA:'具有下列行为之一者，属之： 1.经常拖延工作时限，延误工作进度。 2.时限一到，草草了事，品质欠佳。 3.需要一再地提醒督促，次啊能勉强按时完成工作 4.予人[毫无时间与进度的观念]的印象。',optionB:'凡合乎下列所有行为者，属之： 1.经常能按时完成所交付的工作。 2.对于所安排的工作进度能努力维持，不会借故故意拖延。 ',optionC:'凡通过b之标准外，并具下列行为之一者，属之： 1.对工作时间的安排特别用心，不会为了赶时间匆匆忙忙。 2.提前完成所交付的工作。 '},{id:'A31',name:'自我批评',domain:'101',description:'灾工作前能正确地判断自己是否有可胜任该项工作，工作后对品质能作接近事实的自我批评。',assessMethod:'从日常工作中（或设计情境)观察评量之',optionA:'具有下列行为之一者，属之： 1.对完成该项作业的能力缺乏自我评估的能力（指正确性20%以下）。 2.多完成后的工作品质缺乏自我评估的能力（指正确性10%以下）。 3.虽然再三提醒，也难能养成事前与事后进行自我评估的习惯与能力。',optionB:'凡合乎下列所有行为者，属之： 1.当要求其在工作前对自己是否能胜任该项工作预作评估，其正确性在50%以上。。 2.当要其在工作后对作业结果的品质自行评估，其正确性50%以上。 ',optionC:'凡通过b之标准外，并具下列行为之一者，属之： 1.自动自发地在工作前与工作后进行自我能力及品质的评估。 2.评估的正确性高达80%以上。'},{id:'A32',name:'请求协助',domain:'101',description:'面对工作的困难，能诚恳、适当地请教工作伙伴或上司，不会任由困难存在，耽误工作。',assessMethod:'从日常工作中（或设计情境)观察评量之',optionA:'具有下列行为之一者，属之： 1.遇到苦难你时，无法请求他人协助。 2.请求他人协助时，不能以适当的方式或态度表达。 ',optionB:'凡合乎下列所有行为者，属之： 1.当工作遇到困难时（如缺乏资料、技术等）能向有关的人求助。 ',optionC:'凡通过b之标准外，并具下列行为之一者，属之： 1.能以诚恳的态度请求协助。 2.能以适当的方法，有效地请求协助。 3.当别人协助他时，能适当地表示谢意。'},{id:'A33',name:'安全习惯',domain:'101',description:'工作中具有高度的[安全意识]，并确实遵守安全规定，能察觉工作中的危险状况，并预作防范。',assessMethod:'从日常生活中观察评量之',optionA:'具有下列行为之一者，属之： 1.经常有发生意外的记录。 2.缺乏[危险意识]，导致险象环生。 3.对于环境中的危险，缺乏观察的能力，导致经常发生意外或有惊无险的事。 4.需要经常性的监督与提醒才能确保安全。',optionB:'凡合乎下列所有行为者，属之： 1.能注意遵守工作场中的安全规定。 2.具有高度的[危险意识]。 3.对环境中明显的危险性可以正确察觉，并小心防范（如：火、化学药品、尖锐物等）',optionC:'凡通过b之标准外，并具下列行为之一者，属之： 1.对于环境潜藏的危险，也能敏锐地察觉，并加以防范（如：落物、负重限度、倒塌等）。 2.在工作场中没有发生 意外的记录。'},{id:'A34',name:'收拾的习惯',domain:'101',description:'工作过后或告一段落时，有收拾现场的习惯，借用或取用的物品有归还或物归原位的习惯。',assessMethod:'从日常工作中观察评量之',optionA:'具有下列行为之一者，属之： 1.工作后没有收拾的习惯。 2.虽经再三提醒，也能养成收拾的习惯。 3.工作现场杂乱无章，险像环生。 4.借用的物品随意弃置，没有归还的习惯。 5.没有物归原主的习惯',optionB:'凡合乎下列所有行为者，属之： 1.主动收拾现场的习惯，如：打扫、整理、搁置、定位（偶发者不计）。 2.借用的物品主动归还。 3.各种物品主动归置原位，并做整理。',optionC:'凡通过b之标准外，并具下列行为之一者，属之： 1.在工作过程中，注意各项物品的取放位置，保持工作场所有条不紊的状况。 2.在工作中每告一段落，就会主动进行小收拾，以维持工作场所的整洁。'}];

